import { useState } from 'react';
import { useParams } from 'react-router-dom';
import Comentarios from './Comentarios';
import RevistaPDF from './RevistaPDF';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload } from '@fortawesome/free-solid-svg-icons';

import { useAppDispatch } from '../../redux/hooks';
import { RootState } from '../../redux/rootReducer';
import { fetchCommentParentsAll } from '../../redux/ducks/commentParentsSlice';

export interface InterfaceRevistaPaginaIndividual {
  commentParents: any;
  logged: boolean;
}
export interface InterfaceCommentParents {
  id: number;
  comment_content: string;
  fk_users: number;
  fk_parent: number;
  fk_revistas: number;
  comment_date: string;
  upvotes: number;
  downvotes: number;
  username: any;
}

export interface ParamTypes {
  revistaId: string;
}

const RevistaPaginaIndividual = (props: InterfaceRevistaPaginaIndividual) => {
  const [downloadIconColor, setDownloadIconColor] = useState('white');
  const logged = props.logged;
  const { revistaId } = useParams<ParamTypes>();
  console.log('revistaId ->', revistaId);

  // console.log('revistaId ->', params.revistaId);
  console.log('props RevistaPaginaIndividual->', props);
  // const {storeComments} = props.comments;

  const dispatch = useAppDispatch();
  const comentParentsStore = props.commentParents;

  console.log('comentParentsStore ->', comentParentsStore);

  if (comentParentsStore.loading === 'idle')
    // @ts-ignore
    dispatch(fetchCommentParentsAll({ revistaId }));

  // necessário para definir no textarea
  const textarea_rows = 2;

  return (
    <section className="parede parede-clara pt-0">
      <RevistaPDF />
      {/* <SinglePage pdf={RevistaPDF} /> */}

      <div className="mb-2">
        {/* alterar em prod para o url certo */}
        <a
          href="http://rad.web.ua.pt/static/media/revista_rad_00.da80c72c.pdf"
          download
          className="text-download"
        >
          <FontAwesomeIcon
            icon={faFileDownload}
            size="3x"
            color={downloadIconColor}
            onMouseEnter={() => setDownloadIconColor('#faa50a')}
            onMouseLeave={() => setDownloadIconColor('white')}
            className="pe-1"
          />
          Descarregar a revista
        </a>
      </div>

      <form>
        <label htmlFor="comentario">Comentários</label>
        <textarea
          name="comentario"
          id="comentario"
          placeholder="Junta-te à discussão..."
          rows={textarea_rows}
          required
        ></textarea>
        {!logged && (
          <p>
            Se quiser dizer quem é quando comentar tem de ter sessão iniciada.
          </p>
        )}

        <div className="checkbox">
          <input
            type="checkbox"
            // className="checkbox"
            id="anonimato"
            name="anonimato"
            aria-describedby="anonimato"
            aria-label="Escolher se quer anonimato"
          />
          <label htmlFor="anonimato" className="text-black">
            Quero permanecer anónimo
          </label>
        </div>

        <input
          type="submit"
          name="submit"
          value="PUBLICAR"
          className="btn bg-transparency bg-transparency-red"
        />
      </form>
      {comentParentsStore.commentParentsList.map(
        (commentParent: InterfaceCommentParents) => {
          console.log('comentParentsStore.commentParentsList.map');
          console.log('commentParent ->', commentParent);
          console.log('commentParent.id ->', commentParent.id);
          return (
            <Comentarios
              id={commentParent.id}
              comment_content={commentParent.comment_content}
              comment_date={commentParent.comment_date}
              downvotes={commentParent.downvotes}
              fk_revistas={commentParent.fk_revistas}
              fk_users={commentParent.fk_users}
              upvotes={commentParent.upvotes}
              key={commentParent.id}
              username={commentParent.username}
              id_revista={{ revistaId }}
            />
          );
        }
      )}
    </section>
  );
};

const mapStateToProps = (state: RootState) => ({
  commentParents: state.commentParents,
  logged: state.auth.isLogged,
  // loading: state.league.loading,
});

// connects the component to the store
export default connect(mapStateToProps)(RevistaPaginaIndividual);
