import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
// import { schema, normalize } from 'normalizr';
// import { produce } from 'immer';

const API_IP = process.env.REACT_APP_API_IP;

export interface InterfaceFetchcommentParentsAll {
  revistaId: number;
}

export const fetchData = (url: string) => {
  const axios = require('axios').default;
  return axios
    .get(url)
    .then((response: any) => response.data)
    .catch((err: string) => {
      console.log(err);
      return err;
    });
};
export const getAllData = (urls: Array<string>) =>
  Promise.all(urls.map(fetchData));

export const fetchCommentParentsAll = createAsyncThunk(
  'comments/requestStatus',
  async (params: InterfaceFetchcommentParentsAll, thunkAPI) => {
    console.log('params.revistaId ->', params.revistaId);
    const revistaId = params.revistaId;
    const page = 1;
    const limit = 11000;
    const axios = require('axios').default;
    const endpoint = `http://${API_IP}/revistaRad/public/REVISTARAD/index.php/comments/parentcomments/${revistaId}?page=${page}&limit=${limit}`;
    // const endpoint = `http://${API_IP}/revistaRad/public/REVISTARAD/index.php/comments/1`;
    // const endpoint = `http://${API_IP}/revistaRad/public/REVISTARAD/index.php/jwtauth/verifyacess`;
    // const endpoint =
    //   'http://52.15.37.213/revistaRad/public/REVISTARAD/index.php/comments/9';

    const response = await axios
      // .delete(endpoint)
      .get(endpoint)
      .then((res: any) => {
        console.log('response from call delete ->', res);
        return res;
      })
      .catch((err: string) => {
        console.log('err delete ->', err);
        return err;
      });
    return {
      revistaId: revistaId,
      commentParentsList: response.data,
    };
  }
);
// https://redux-toolkit.js.org/tutorials/typescript
const commentParentsSlice = createSlice({
  name: 'commentParents',
  initialState: {
    revistaId: 0,
    commentParentsList: [],
    loading: 'idle',
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(fetchCommentParentsAll.pending, state => {
        state.loading = 'pending';
      })
      .addCase(fetchCommentParentsAll.fulfilled, (state, action) => {
        state.loading = 'success';
        state.revistaId = action.payload.revistaId;
        state.commentParentsList = action.payload.commentParentsList;
        //
      })
      .addCase(fetchCommentParentsAll.rejected, (state, action) => {
        state.loading = 'rejected';
        //
      });
  },
});

export default commentParentsSlice.reducer;
