import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
// import { schema, normalize } from 'normalizr';
// import { produce } from 'immer';

const API_IP = process.env.REACT_APP_API_IP;

export const fetchData = (url: string) => {
  const axios = require('axios').default;
  return axios
    .get(url)
    .then((response: any) => response.data)
    .catch((err: string) => {
      console.log(err);
      return err;
    });
};
export const getAllData = (urls: Array<string>) =>
  Promise.all(urls.map(fetchData));

export const fetchRevistasAll = createAsyncThunk(
  'revistas/requestStatus',
  async () => {
    const axios = require('axios').default;
    const endpoint = `http://${API_IP}/revistaRad/public/REVISTARAD/index.php/revistas`;
    //  const endpoint = `http://${API_IP}/revistaRad/public/REVISTARAD/index.php/comments/parentcomments/${revistaId}?page=${page}&limit=${limit}`;
    const response = await axios
      .get(endpoint)
      .then((res: any) => {
        console.log('response from call ->', res);
        return res;
      })
      .catch((err: string) => {
        console.log('err ->', err);
        return err;
      });
    return response.data;
  }
);
// https://redux-toolkit.js.org/tutorials/typescript
const revistasSlice = createSlice({
  name: 'commentParents',
  initialState: {
    revistaList: [],
    loading: 'idle',
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(fetchRevistasAll.pending, state => {
        state.loading = 'pending';
      })
      .addCase(fetchRevistasAll.fulfilled, (state, action) => {
        state.loading = 'success';
        state.revistaList = action.payload;
        //
      })
      .addCase(fetchRevistasAll.rejected, (state, action) => {
        state.loading = 'rejected';
        //
      });
  },
});

export default revistasSlice.reducer;
