// https://redux-toolkit.js.org/tutorials/typescript
const threadsSlice = createSlice({
  name: 'commentParents',
  initialState: {
    commentParentId: 0,
    commentList: [],
    loading: 'idle',
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(fetchThreadsAll.pending, state => {
        state.loading = 'pending';
      })
      .addCase(fetchThreadsAll.fulfilled, (state, action) => {
        state.loading = 'success';
        state.commentParentId = 0;
        // state.commentList = action.payload.commentParentsList;
      })
      .addCase(fetchThreadsAll.rejected, (state, action) => {
        state.loading = 'rejected';
        //
      });
  },
});

export default threadsSlice.reducer;
