import React from 'react';
export interface InterfaceRevistasListaIndividual {
  titulo: string;
  imagem: string;
  numeracao: number;
  key: string;
}

export default function RevistasListaIndividual(
  props: InterfaceRevistasListaIndividual
) {
  console.log('props.numeracao ->', props.numeracao);
  console.log('props.numeracao % 2 == 0->', props.numeracao % 2 === 0);

  let parede = null;
  console.log('window.innerWidth ->', window.innerWidth);

  parede =
    props.numeracao % 2 === 0
      ? ' parede parede-clara '
      : ' parede parede-escura ';

  const revistaClassName = parede + ' listagem-revistas-item';
  console.log('revistaClassName ->', revistaClassName);

  const url = '/revistas/' + props.numeracao + '/';
  return (
    <article className={revistaClassName}>
      {/* <h1 className="text-center">{props.titulo}</h1> */}
      <img
        src={props.imagem}
        className="revista-capa"
        alt={props.titulo}
        onClick={() => window.location.replace(url)}
      />
      <a href={url} role="button" className="numeracao">
        #{props.numeracao}
      </a>
    </article>
  );
}
